header {
    background-color: #333;
    color: #fff;
    padding: 10px 0;
    border-radius: 20px;
    /* Adjust the value as needed */
    overflow: hidden;
    /* Ensures child elements respect rounded corners */
}

.container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1200px;
    margin: auto;
    padding: 20px;
}

h1 {
    margin: 0;
    font-size: 2.8rem;
    font-weight: bold;
}

h1 a {
    color: #fff;
    text-decoration: none;
}

h1 a:hover {
    text-decoration: underline;
}

nav ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
    display: flex;
}

nav ul li {
    margin-right: 20px;
}

nav ul li:last-child {
    margin-right: 0;
}

nav ul li a {
    color: #fff;
    text-decoration: none;
    font-size: 1.8rem;
}

nav ul li a:hover {
    text-decoration: underline;
}