.blog {
    display: flex;
    justify-content: center;
    width: 100%;
    padding: 20px;
}

.container {
    max-width: 1200px;
    margin: 0 auto;
}

.blog-section {
    width: 100%;
    max-width: 800px;
    /* Adjust as needed */
    margin-right: 20px;
    /* Space between sections */
}

.youtube-section {
    width: 100%;
    max-width: 800px;
    /* Adjust as needed */
}

.article {
    background-color: #fff;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    margin-bottom: 20px;
}

.article h3 {
    font-size: 1.5rem;
    margin-bottom: 10px;
}

.article p {
    margin-bottom: 10px;
    line-height: 1.6;
}

.read-more-button {
    background-color: #61dafb;
    color: #fff;
    border: none;
    padding: 10px 20px;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
    border-radius: 5px;
}

.read-more-button:hover {
    background-color: #47a7f5;
}

.youtube-video {
    margin-bottom: 20px;
}